@charset "UTF-8";
.swiper_stepper {
  overflow: hidden;
  background: gray;
  margin: 50px;
}
.swiper_stepper .dosirak_area {
  margin: 0 auto;
  width: 80%;
  height: 200px;
  background: gray;
}
.swiper_stepper .dosirak_area .dosirak {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  width: 100%;
  height: 100%;
}
.swiper_stepper .dosirak_area .dosirak li {
  background: purple;
  border: 1px solid gray;
  font-size: 20px;
}
.swiper_stepper .stepper_swiper {
  overflow: visible;
  margin-top: 100px;
}
.swiper_stepper .stepper_swiper .swiper-slide ul {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.swiper_stepper .stepper_swiper .swiper-slide ul li {
  margin: 10px;
}
.swiper_stepper .stepper_swiper .swiper-slide ul li label div {
  font-size: 20px;
  padding: 50px 0;
  background: yellowgreen;
}
.swiper_stepper .stepper_swiper .swiper-slide ul li input {
  display: none;
}
.swiper_stepper .stepper_swiper .swiper-slide ul li input:checked + div {
  background: yellow;
}
.swiper_stepper .stepper_swiper .swiper-button-next,
.swiper_stepper .stepper_swiper .swiper-button-prev {
  top: -30%;
  color: yellow;
}
.swiper_stepper .stepper_swiper .swiper-button-next {
  pointer-events: none;
  opacity: 0.5;
}
.swiper_stepper .stepper_swiper .swiper-button-next.on {
  pointer-events: unset;
  opacity: 1;
}
.swiper_stepper .stepper_swiper .swiper-pagination {
  bottom: 110%;
}
.swiper_stepper .stepper_swiper .swiper-pagination-bullet {
  background-color: yellow;
  width: 30px;
  height: 30px;
  bottom: 100%;
}
.swiper_stepper .stepper_swiper .swiper-pagination-bullet::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0%;
  transform: translateY(-50%);
  width: 100%;
  height: 3px;
  background-color: #fff;
}
.swiper_stepper .stepper_swiper .swiper-pagination-bullet {
  position: relative;
  background-color: skyblue;
  width: 50px;
  height: 50px;
  opacity: unset;
}
.swiper_stepper .stepper_swiper .swiper-pagination-bullet::before {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  font-size: 14px;
  line-height: 1;
}
.swiper_stepper .stepper_swiper .swiper-pagination-bullet.swiper-pagination-bullet-active {
  background-color: yellow;
}
.swiper_stepper .stepper_swiper .swiper-pagination-bullet:nth-child(1)::before {
  content: "밥";
  display: flex;
  align-items: center;
  justify-content: center;
}
.swiper_stepper .stepper_swiper .swiper-pagination-bullet:nth-child(2)::before {
  content: "메인반찬";
}
.swiper_stepper .stepper_swiper .swiper-pagination-bullet:nth-child(3)::before {
  content: "서브반찬";
}
.swiper_stepper .stepper_swiper .swiper-pagination-bullet:nth-child(4)::before {
  content: "채소/김치";
}
.swiper_stepper .stepper_swiper .swiper-pagination-bullet:nth-child(5)::before {
  content: "완료";
}
.swiper_stepper .stepper_swiper .swiper-pagination-bullet.active {
  background-color: yellow; /* active된 bullet의 배경 색상 */
}
.swiper_stepper .stepper_swiper .swiper-button-disabled {
  pointer-events: none !important;
  opacity: 0 !important;
}